
.wrapper {
   width: 100vw;
   height: 100vh;
   position: absolute;
  font-family: 'Cormorant Garamond', serif;
}
.back {
    position:fixed ; 
    left: 1vw;
    top: 5vh;
    color: black;
    font-family: 'Lato';
    text-decoration: none;
    font-weight: bold;
}
.About {
  position: relative;
    font-size: 4vw !important;
   margin: 0 auto;
    text-align: center;
  top: 5vh;
  width: 50vw;
  height: auto;
  display: -ms-flexbox;
  display: flex;
}
section {
  width: 100%;
  display: inline-block;
 
  
  font-family: 'Cormorant Garamond', serif;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
  text-decoration: none;
}
p {
   font-size: 2vw;
}



